  margin: 10px;
  background-color: white;
  border: 10px solid #241e19;
}

p {
  display: flex;
  text-shadow: $shadow;
  justify-content: center;
  justify-self: center;
}

.links {
  color: white;
  display: flex;
  justify-content: center;
  margin: 20px;
  padding: 0px 20px 20px;
}
